import React, { useEffect } from 'react'
import Header from "./../../Header/Header"
import Trading_breadcum from "./Trading_breadcum/Trading_breadcum"
import Trading_first_sec from "./Trading_first_sec/Trading_first_sec"
import Footer from "./../../FOOTER/Footer"
// import {Tabtitle} from "./../../Tabtitle"
import {SeoContext} from "./../../../Seocontext"


const Trading = () => {
  useEffect(()=>{
    window.scrollTo({top:0, behavior:'smooth'})
  },[])
  // Tabtitle("Trading - TRG : Raichand")
  return (
    <div>
      <SeoContext>
        <Header/>
        <Trading_breadcum/>
        <Trading_first_sec/>
        <Footer/>
        </SeoContext>
    </div>
  )
}

export default Trading