import React from 'react'
import { Link } from 'react-router-dom'
//css file
import "./trading_breadcum.css"
// images
import {} from "./../../../../assets/images/breadcum/trading.png"


const Trading_breadcum = () => {
    return (
        <div>
            <div className='trading_breadcrumb'>
                <div className='gradint_effect'></div>
                <div className='text-white trading_bread_text'>
                    <h1 className='trading_bread_head'>Raichand Trading</h1>
                    <div className='flex justify-center items-center gap-2 mt-1'>
                        <Link to="/" className='trading_breadcum_link'><span>Home</span></Link>
                        <span>&#187;</span>
                        <Link to="/raichand-trading" className='text-white'><span>Raichand Trading</span></Link>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Trading_breadcum